	module.exports = "<div id=\"about-us-container\">\r\n\t\r\n\t<div class=\"container\">\t\t\r\n\t\t<h1>Exclusive Venue for Exhibitions for Product Launches</h1>\t\t\r\n\t</div>\r\n\t<div class=\"image-header\">\r\n\t\t<auto-scroller>\r\n\t\t\t<img src=\"" + __webpack_require__(542) + "\" alt=\"\" />\r\n\t\t</auto-scroller>\r\n\t</div>\r\n\t<div class=\"container\">\r\n\t\t<p class=\"lead\">\r\n\t\t\tHeld thrice a year, in luxurious five star hotels in the city, it brings together designers from all over the world for a two day show.Vimonisha Exhibitions enjoys the prestige of being Chennai’s most premium designer exhibition and the pioneer of designer exhibitions in Chennai, and takes pride in its impressive repertoire of designers that they have provided a platform to, and launched through their much awaited exhibitions repeatedly for the last 25 years.\t\t\t\r\n\t\t</p>\r\n\t\t<blockquote>\r\n\t\t\tVimonisha exhibtions organises high end designer shows in chennai for the last 25 years, and is chennai's most awaited and premium exhibition.\r\n\t\t</blockquote>\t\r\n\t\t<h3>Story</h3>\r\n\t\t<p class=\"lead\">\r\n\t\t\tVimonisha Private Collection is a vision of mother and daughter Vimal Shivdasanil and Monisha Gidwanii, who ran\r\n\t\t\ta very successful gallery in Chennai called Vimonisha Gallery and examined the need to promote designers and handle their marketing and publicity. Beginning with only five designers in each exhibition, Vimonisha exhibitions has completed 75 shows and now has more than 55 designers at each show. The Vimonisha Private Collection held thrice a year in Chennai is a reflection of Monisha’s tastes and fashion preferences, and is a carefully handpicked eclectic collection of the nation’s best talent, which she successfully romotes in the south of India.\r\n\t\t</p>\r\n\t\t<div class=\"col-sm-12 text-center\">\r\n\t\t\t<p>\r\n\t\t\t<input type=\"button\" name=\"details\" class=\"btn btn-xs btn-click\" value=\"View details\" (click)=\"showDetails('test-1')\">\r\n\t\t\t</p>\r\n\t\t</div>\r\n\t</div>\r\n\t<div class=\"clear-fix\"></div>\r\n</div>\r\n\r\n<slider direction=\"UP\" id=\"test-1\">\r\n\t<div class=\"container\">\r\n\t\t<div class=\"row\">\r\n\t\t\t<div class=\"col-sm-12\">\r\n\t\t\t\t<h3>Company Overview: Slider</h3>\r\n\t\t\t\t<p class=\"lead\">\r\n\t\t\t\t\tThe Vimonisha Private Collection , promoted by Vimonisha exhibitions is Chennai's most awaited designer show, and has completed more than 75 such shows. Held thrice a year, in luxurious five star hotels in the city, it brings together designers from all over the world for a two day show.Vimonisha Exhibitions enjoys the prestige of being Chennai’s most premium designer exhibition and the pioneer of designer exhibitions in Chennai, and takes pride in its impressive repertoire of designers that they have provided a platform to, and launched through their much awaited exhibitions repeatedly for the last 25 years. \r\n\t\t\t\t</p>\r\n\t\t\t\t<p class=\"lead\">\r\n\t\t\t\t\tDesigners from across India are handpicked and invited to showcase their products ranging from clothes in prêt, couture, accessories, jewelry and lifestyle products under one roof and is Chennai's most sought after exhibtion among the creme de la creme of Chennai's high society ladies, fashion icons, film actresses, industrialists, international expat ladies, bureaucrats, politicians and export buying houses and merchandisers alike.Designers participating in Vimonisha's exhibitions can expect sales from both the retail trade as well as whole sale enquiries and business from leading fashion houses, boutiques and stores from South India. Thus it provides the perfect platform to designers looking at expanding their business in the south of India.Curated by Monisha Gidwani CEO of Vimonisha, this collection will see the finest of talent from across the country where more than 60 innovative designers par excellence will converge to showcase their new creations.\r\n\t\t\t\t</p>\r\n\t\t\t\t<blockquote>\r\n\t\t\t\t\tFor bookings write to monisha@vimonisha.com\r\n\t\t\t\t</blockquote>\r\n\t\t\t\t<p class=\"lead\">\r\n\t\t\t\t\tFashionistas, fashion trend watchers, shoppers, traders, export houses , life style store owners and merchandisers from across south India wait eagerly every quarter to see what Vimonisha has in store for them and what new they offer at the hugely popular multi designer booth fashion expo. Vimonisha never fails to disappoint as it brings the latest show stoppers to Chennai each time.\r\n\t\t\t\t</p>\r\n\t\t\t\t<p class=\"lead\"> \r\n\t\t\t\t\tVimonisha Private Collection is a vision of mother and daughter VimalShivdasani and Monisha Gidwani, who ran a very successful gallery in Chennai called Vimonisha Gallery and examined the need to promote designers and handle their marketing and publicity. Beginning with only five designers in each exhibition, Vimonisha exhibitions has completed 75 shows and now has more than 55 designers at each show. The Vimonisha Private Collection held thrice a year in Chennai is a reflection of Monisha’s tastes and fashion preferences, and is a carefully handpicked eclectic collection of the nation’s best talent, which she helps promote in the south of India.\r\n\r\n\t\t\t\t</p>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t</div>\r\n</slider>\r\n";

